.name-container {
  display: flex;
  flex-direction: column;
  justify-content: left;
  padding-top: 3rem;
}

@media (max-width: 768px) {
  .name-container {
    padding: 0rem;
  }
}

.name {
  font-size: 2rem;
  font-weight: bold;
  margin-bottom: 1rem;
}

.about-links-container {
  display: flex;
  flex-direction: row;
  justify-content: left;
  align-items: center;
  gap: 2rem;
}

@media (max-width: 768px) {
  .about-links-container {
    flex-direction: column;
    align-items: flex-start;
    gap: 0px;
  }
}

.about-links-container a {
  text-decoration: none;
  color: var(--accent-color);
  font-weight: 600;
}

.about-icons-container {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  gap: 3rem;
  padding-top: 2rem;
}
